<template>
    <WScrollPanel
        ref="wsp"
        :ratio.sync="scrollRatio"
        :viewHeight="viewHeight"
        :contentHeight="itemsHeight"
        @change="scrollItems"
    >

        <template v-for="(item,kitem) in useItems">
            <div
                ref="wdsDiv"
                :style="`position:absolute; top:${item.screenY}px; width:100%; opacity:${(item.nowShow && item.delayShow)?1:0.001}; ${item.delayShow?'transition:opacity 0.1s':''}`"
                :index="item.index"
                :nowShow="item.nowShow"
                :key="kitem"
            >
                <slot
                    name="block"
                    :row="item.row"
                    :irow="item.index"
                ></slot>
            </div>
        </template>

    </WScrollPanel>
</template>

<script>
import each from 'lodash/each'
import map from 'lodash/map'
import size from 'lodash/size'
// import debounce from 'lodash/debounce'
// import throttle from 'lodash/throttle'
import cint from 'wsemi/src/cint.mjs'
import genID from 'wsemi/src/genID.mjs'
import genPm from 'wsemi/src/genPm.mjs'
import delay from 'wsemi/src/delay.mjs'
import isarr from 'wsemi/src/isarr.mjs'
import binarySearch from '../js/binarySearch.mjs'
import globalMemory from '../js/globalMemory.mjs'
import WScrollPanel from './WScrollPanel.vue'

//gm
let gm = globalMemory()

/**
 * @vue-prop {Array} [rows=[]] []slotslotrowirowrowsslotmargin
 * @vue-prop {Number} [viewHeight=400] px400
 * @vue-prop {Number} [itemMinHeight=24] px24
 * @vue-prop {Number} [itemsPreload=40] 40
 */
export default {
    components: {
        WScrollPanel,
    },
    props: {
        rows: {
            type: Array,
            default: () => [],
        },
        viewHeight: {
            type: Number,
            default: 400,
        },
        itemMinHeight: {
            type: Number,
            default: 24,
        },
        itemsPreload: {
            type: Number,
            default: 40,
        },
    },
    data: function() {
        return {
            //itemDivstylewidth:100%ie11flex
            mmkey: null,
            changeHeight: true, //, true
            scrollRatio: 0, //
            scrollInfor: null, //
            scrollToEnd: false, //, refresh
            itemsHeight: 0, //
            useItems: [], //
        }
    },
    beforeDestroy: function() {
        //console.log('beforeMount')

        let vo = this

        //remove
        gm.remove(vo.mmkey)

    },
    mounted: function() {
        //console.log('mounted')

        let vo = this

        //refresh, watchchange, mounted
        vo.refresh('mounted')

    },
    watch: {

        rows: {
            immediate: true,
            deep: true,
            handler(value) {
                //console.log('watch rows', value)

                let vo = this

                //check
                if (size(value) === 0) {
                    return
                }

                //changeRows
                vo.changeRows(value)

            }
        }

    },
    computed: {
    },
    methods: {

        changeRows: function(rows) {
            //console.log('methods changeRows', rows)

            let vo = this

            //check
            if (!isarr(rows)) {
                let msg = 'rows is not array'
                //console.log(msg)
                return msg
            }
            if (size(rows) === 0) {
                let msg = 'rows is empty'
                //console.log(msg)
                return msg
            }

            //mmkey
            if (vo.mmkey === null) {

                //mmkey
                vo.mmkey = genID()

            }
            else {

                //remove
                gm.remove(vo.mmkey)

            }

            //items, lodashnew Arrayfor+push
            let items = map(rows, (v, k) => {
                return {
                    index: k,
                    height: vo.itemMinHeight,
                    y: k * vo.itemMinHeight,
                    // screenY: 0, //y
                    // nowShow: false, //
                    row: v,
                }
            })

            //save
            //vo.items = items
            gm.set(vo.mmkey, items)

            //refresh
            vo.refresh('changeRows')

        },

        refresh: async function(from) {
            //console.log('methods refresh', from)

            let vo = this

            //check
            if (vo.mmkey === null) {
                return
            }

            //core
            let n = 0
            let limit = 3
            async function core() {
                let pm = genPm()

                //n
                n += 1

                // //check, 
                // if (n >= limit && vo.scrollInfor !== null) {
                //     if (vo.scrollInfor.ch !== vo.itemsHeight) {
                //         console.log('', JSON.stringify(vo.scrollInfor), vo.scrollInfor.ch, vo.itemsHeight)
                //     }
                // }

                //check, , 
                if (n > limit) {
                    //console.log(`refresh ${limit} , `)
                    pm.resolve(false)
                    return pm
                }

                //genUseItems
                vo.genUseItems()

                //delay
                await delay(1)

                let b = vo.updateItems()
                pm.resolve(b)

                return pm
            }

            async function call() {

                //, , , , , 
                let r = await core()
                while (r) {
                    r = await core()
                }

            }

            //call
            await call()

            //genUseItems
            vo.genUseItems()

            //delayShow
            for (let k = 0; k < size(vo.useItems); k++) {
                let v = vo.useItems[k]
                if (!v.delayShow) {
                    v.delayShow = true
                }
            }

        },

        genUseItems: function() {
            //console.log('methods genUseItems')

            let vo = this

            //check
            if (vo.mmkey === null) {
                return
            }

            //default scrollInfor
            if (vo.scrollInfor === null) {
                vo.scrollInfor = {
                    r: 0,
                    t: 0,
                    b: vo.viewHeight,
                    ch: vo.itemsHeight,
                }
            }

            //items
            //let items = vo.items
            let items = gm.get(vo.mmkey)

            //n
            let n = size(items)

            //indStart, 
            let indStartActual = binarySearch(items, (ind) => {
                let v = items[ind]
                let dy = vo.scrollInfor.t - (v.y + v.height)
                return dy
            })
            if (indStartActual === null) {
                indStartActual = 0
            }
            let indStart = Math.max(indStartActual - vo.itemsPreload, 0)

            //indEnd, 
            let indEndActual = binarySearch(items, (ind) => {
                let v = items[ind]
                let dy = vo.scrollInfor.b - v.y
                return dy
            })
            if (indEndActual === null) {
                indEndActual = n - 1
            }
            let indEnd = Math.min(indEndActual + vo.itemsPreload, n - 1)

            //delayShow
            let delayShow = false
            let m = size(vo.useItems)
            if (m > 0) {
                let bIndexStart = vo.useItems[0].index === indStart
                let bIndexEnd = vo.useItems[m - 1].index === indEndActual
                delayShow = bIndexStart && bIndexEnd
            }

            //useItems
            let useItems = []
            for (let k = indStart; k <= indEnd; k++) {
                let v = {
                    ...items[k]
                }
                v.screenY = v.y - vo.scrollInfor.t //ypx
                v.nowShow = k >= indStartActual //
                v.delayShow = delayShow //, 
                useItems.push(v)
            }

            //save
            vo.useItems = useItems

        },

        updateItems: function() {
            //console.log('methods updateItems')

            let vo = this

            //check
            if (vo.mmkey === null) {
                return false
            }

            //items
            //let items = vo.items
            let items = gm.get(vo.mmkey)

            //n
            let n = size(items)

            //update height
            each(vo.$refs.wdsDiv, (v) => {
                if (v.getAttribute) {
                    let nowShow = v.getAttribute('nowShow')
                    if (nowShow) {
                        let index = cint(v.getAttribute('index'))
                        if (index >= 0 && index < n) {
                            let h = v.clientHeight //margin
                            if (items[index].height !== h) {
                                items[index].height = h
                                vo.changeHeight = true
                            }
                        }
                    }
                }
            })

            //check
            let b = vo.changeHeight
            if (b) {

                //update y
                let y = 0
                for (let i = 0; i < n; i++) {
                    let v = items[i]
                    if (v.y !== y) {
                        v.y = y
                    }
                    y += v.height
                }

                //update itemsHeight
                if (vo.itemsHeight !== y) {
                    vo.itemsHeight = y
                }

                //reset
                vo.changeHeight = false

            }

            return b
        },

        scrollItems: async function(e) {
            //console.log('methods scrollItems', e)

            let vo = this

            //check
            if (vo.mmkey === null) {
                return
            }

            //save
            vo.scrollInfor = e

            //refresh
            vo.refresh('scrollItems')

            //check, , 
            if (!vo.scrollToEnd && e.r === 1) {

                //lock
                vo.scrollToEnd = true

                //delay
                await delay(100)

                //triggerEvent
                vo.$refs.wsp.triggerEvent()

                //delay
                await delay(100)

                //uplock, 
                vo.scrollToEnd = false

            }

        },

    },
}
</script>

